import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {PageItem} from "@component/wizard/page-item";
import {StringUtil} from "@util/string-util";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.shutdown.wizard.summaryPage.title"),
)
@Component({
   templateUrl: "summary-page.html",
   styleUrls: ["summary-page.scss"],
})
export class SummaryPageComponent implements OnInit {

   reasons: Map<string, string> = new Map<string, string>([
      ["custom", "vsan.cluster.shutdown.wizard.summaryPage.shutDownReason.custom.label"],
      ["maintenance", "vsan.cluster.shutdown.wizard.summaryPage.shutDownReason.maintenance.label"],
      ["troubleshoot", "vsan.cluster.shutdown.wizard.summaryPage.shutDownReason.troubleshoot.label"],
      ["upgrade", "vsan.cluster.shutdown.wizard.summaryPage.shutDownReason.upgrade.label"],
   ]);
   reasonKeys: string[] = [...this.reasons.keys()];

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   isVcOnVsan: boolean;

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.cluster.shutdown.wizard.summaryPage.emptyReason.error")))
   get shutdownReason(): string {
      return this.isCustomReasonSelected ?
            this.customReason :
            VsanUiUtils.getString(this.reasons.get(this.selectedReason));
   }

   selectedReason: string;
   customReason: string;

   async ngOnInit(): Promise<void> {
      // by default select the type custom reason
      this.selectedReason = this.reasons.keys().next().value;
      (this.pageControls as PageItem).nextButtonIsDisabled = true;
   }

   get isCustomReasonSelected(): boolean {
      return this.selectedReason === this.reasons.keys().next().value;
   }

   get powerOffVmsMessage(): string {
      return this.isVcOnVsan ?
            VsanUiUtils.getString("vsan.cluster.shutdown.wizard.summaryPage.steps.powerOffVms.vcOnVsan") :
            VsanUiUtils.getString("vsan.cluster.shutdown.wizard.summaryPage.steps.powerOffVms");
   }

   shutdownReasonChanged(): void {
      (this.pageControls as PageItem).nextButtonIsDisabled = this.shutdownReason === undefined ||
            this.shutdownReason.trim() === "";
   }
}
