import {ManagedObject} from "@service/managed-object";
import {TaskStatus} from "@service/task-monitor-service";

/**
 * Holds info for a status change initiated by the current user on the vSAN cluster.
 * This data is stored in the Browser local storage, check <code>VsanStatusService</code>
 * and is accessible across iframes.
 */
export class VsanStatusChangeData {

   /**
    * A cluster managed object reference associated with this status change
    */
   clusterRef: ManagedObject;

   /**
    * A task moref, that is changing the status of the vSAN cluster.
    */
   taskRef: ManagedObject;

   /**
    * Enum value, holding information about what status change is happening with the vSAN cluster exactly.
    */
   changeType: StatusChangeType;

   /**
    * Upon completion of the task will hold either SUCCESS or ERROR, which could be used by subscribers.
    */
   taskStatus: TaskStatus;

   constructor(taskRef: ManagedObject, status: StatusChangeType,
         clusterRef: ManagedObject = ManagedObject.contextObject) {
      this.taskRef = taskRef;
      this.changeType = status;
      this.clusterRef = clusterRef;
   }
}

/**
 * Enum with codes, used to differentiate different vSAN configuration events like
 * vSAN enabled, cluster shutdown, etc.
 */
export enum StatusChangeType {
   /**
    * A cluster has had the vSAN service enabled.
    */
   VSAN_SERVICE_ENABLED,
   /**
    * A cluster has had the vSAN service disabled.
    */
   VSAN_SERVICE_DISABLED,
   /**
    * A vSAN cluster has been powered on.
    */
   VSAN_CLUSTER_POWER_ON,
   /**
    * A vSAN cluster has been shut down.
    */
   VSAN_CLUSTER_POWER_OFF,
}