import {Pipe, PipeTransform} from "@angular/core";
import {BaseHealthState, VsanHealthUtil} from "@util/vsan-health-util";

@Pipe({
   name: "healthStateLabel",
})
export class VirtualObjectHealthStateLabelPipe implements PipeTransform {
   transform(state: BaseHealthState | string): string {
      return VsanHealthUtil.getVsanObjectHealthStatusText(state);
   }
}