import {Component, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {PrecheckPageComponent} from "./precheck-page.component";
import {VcenterServerNoticePageComponent} from "./vcenter-server-notice-page.component";
import {SummaryPageComponent} from "./summary-page.component";
import {ManagedObject} from "@service/managed-object";
import {ClusterShutdownService} from "@generated/cluster-shutdown-service";
import {VsanStatusService} from "@service/status-updates/vsan-status.service";
import {VsanUiUtils} from "@util/vsan-util";
import {StatusChangeType, VsanStatusChangeData} from "@service/status-updates/vsan-status-change.data";

@Component({
   templateUrl: "cluster-shutdown-wizard.html",
   selector: "cluster-shutdown-wizard",
})
export class ClusterShutdownWizardComponent implements OnInit {
   /**
    * Holds wizard context data, shared between all the pages.
    */
   context: any;
   errorMessage: string;
   services: any[] = [ClusterShutdownService];

   constructor(private vsanStatusService: VsanStatusService) {
   }

   ngOnInit(): void {
      this.setWizardContext();
   }

   private setWizardContext(): void {
      this.context = {
         clusterRef: ManagedObject.contextObject,
      };
   }

   pages: PageSet[] = [
      new PageSet([PrecheckPageComponent]),
      new PageSet([VcenterServerNoticePageComponent], (isVcOnVsan) => isVcOnVsan),
      new PageSet([SummaryPageComponent]),
   ];

   onConfigureFinish(taskRef: ManagedObject): void {
      this.vsanStatusService.storeChange(new VsanStatusChangeData(taskRef, StatusChangeType.VSAN_CLUSTER_POWER_OFF));
      VsanUiUtils.closeModalDialog();
   }
}