import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanHealthData} from "@generated/vsan-health-data";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanTestData} from "@generated/vsan-test-data";
import {PageItem} from "@component/wizard/page-item";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {Logger} from "@util/logger";
import {AskVmwareId} from '@service/navigation/model/ask-vmware-id';
import {ManagedObject} from "@service/managed-object";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.shutdown.wizard.precheckPage.title"),
)
@Component({
   templateUrl: "precheck-page.html",
   styleUrls: ["precheck-page.scss"],
})
export class PrecheckPageComponent implements OnInit {
   readonly AskVmwareId: typeof AskVmwareId = AskVmwareId;

   private static readonly VC_ON_VSAN_TEST_ID: string = "com.vmware.vsan.health.test.vconvsan";
   private static readonly VMS_SHUTDOWN_TEST_ID: string = "com.vmware.vsan.health.test.allvmsshutdown";
   private static readonly MAX_POWERED_ON_VMS: number = 10;

   getHealthIconClass: (val: VsanHealthStatus) => string = VsanHealthUtil.getHealthStatusClass;
   getHealthIconShape: (val: VsanHealthStatus) => string = VsanHealthUtil.getHealthStatusShape;

   VsanHealthStatus: typeof VsanHealthStatus = VsanHealthStatus;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   shutdownPrecheckFailedTests: VsanTestData[];

   @InputProperty("clusterPowerSelfContainSupported")
   isClusterPowerSelfContainSupported: boolean;

   @OutputProperty()
   isVcOnVsan: boolean = false;

   @OutputProperty()
   masterHost: string;

   @OutputProperty()
   infrastructureVMs?: ManagedObject[];

   vmsShutdownTest: VsanTestData;

   private redPrechecksCount: number;

   ngOnInit(): void {
      if (VsanUiUtils.isEmpty(this.shutdownPrecheckFailedTests)) {
         return;
      }

      const vcOnVsanTest: VsanTestData = this.shutdownPrecheckFailedTests.find(test => this.isVcOnVsanTest(test));
      this.isVcOnVsan = !!vcOnVsanTest;
      if (this.isVcOnVsan) {
         this.masterHost = this.getMasterHost(vcOnVsanTest);
         // Rebuild the wizard's TOC to show the VC notice page
         this.pageControls.rebuildToc();
      }
      this.vmsShutdownTest = this.shutdownPrecheckFailedTests.find(test => this.isVmsShutdownTest(test));
      this.redPrechecksCount = this.getRedPrechecksCount();

      (this.pageControls as PageItem).nextButtonIsDisabled = this.isNextButtonDisabled();
   }

   private isVcOnVsanTest(test: VsanTestData): boolean {
      return test.testId === PrecheckPageComponent.VC_ON_VSAN_TEST_ID;
   }

   isVmsShutdownTest(test: VsanTestData): boolean {
      return test.testId === PrecheckPageComponent.VMS_SHUTDOWN_TEST_ID;
   }

   private isNextButtonDisabled(): boolean {
      return this.redPrechecksCount > 0 && !this.vmsShutdownTest
            || this.redPrechecksCount > 1
            || this.vmsShutdownTest && VsanUiUtils.isEmpty(this.infrastructureVMs);
   }

   private getRedPrechecksCount(): number {
      if (VsanUiUtils.isEmpty(this.shutdownPrecheckFailedTests)) {
        return 0;
      }
      const redChecks: VsanTestData[] = this.shutdownPrecheckFailedTests.filter(test => test.status === VsanHealthStatus.red);
      return redChecks ? redChecks.length : 0;
   }

   /**
    * When the VC is on vSAN we need info for the master host, to show it in the vCenter server notice page
    */
   private getMasterHost(vcOnVsanTest: VsanTestData): string {
      if (!vcOnVsanTest
            || VsanUiUtils.isEmpty(vcOnVsanTest.details)
            || VsanUiUtils.isEmpty(vcOnVsanTest.details[0].rows)
            || VsanUiUtils.isEmpty(vcOnVsanTest.details[0].rows[0].rowValues)
            || !vcOnVsanTest.details[0].rows[0].rowValues[0].cellValue) {
         Logger.warn("Couldn't find master host for VC on vSAN cluster!");
         return null;
      }
      return vcOnVsanTest.details[0].rows[0].rowValues[0].cellValue.objectName;
   }

   getVmsShutdownTestAlert(): string {
      return this.showInfrastructureVmsOption()
            ? VsanUiUtils.getString("vsan.cluster.shutdown.wizard.precheckPage.vms.not.shutdown.error.self.contain.cluster")
            : VsanUiUtils.getString("vsan.cluster.shutdown.wizard.precheckPage.vms.not.shutdown.error");
   }

   /**
    * The infrastructure VMs option should be visible if the capability is set and the VC is on vSAN, but it should be
    * hidden if not powered off VMs are more than 10
    */
   showInfrastructureVmsOption(): boolean {
      return this.isClusterPowerSelfContainSupported && this.isVcOnVsan && this.showTestDetails(this.vmsShutdownTest);
   }

   markInfrastructureVms(target: any): void {
      if (target.checked) {
         this.infrastructureVMs = this.vmsShutdownTest.details[0].rows
               .map(row => ManagedObject.create(
                     row.rowValues[0]?.cellValue.object.type,
                     row.rowValues[0]?.cellValue.object.value,
                     row.rowValues[0]?.cellValue.object.serverGuid));
      } else {
         this.infrastructureVMs = null;
      }

      (this.pageControls as PageItem).nextButtonIsDisabled = this.isNextButtonDisabled();
   }

   showTestDetails(test: VsanTestData): boolean {
      if (VsanUiUtils.isEmpty(test.details)) {
         return false;
      }

      // TODO bbambalo [VSANCORE-7763] Check whether the limit of max visualized entities should be applied to the other
      //  tests as well
      return this.isVmsShutdownTest(test)
            ? test.details[0].rows.length <= PrecheckPageComponent.MAX_POWERED_ON_VMS
            : true;
   }
}